/**
 * Beautify Radio and Checkbox with pure CSS. http://forsigner.com/magic-check
 */
@magic-radio-size: 20px;
@magic-checkbox-size: 20px;
@magic-dot-size: 8px;
@magic-border-color: #c0c0c0;
@magic-main-color: #3e97eb;
@magic-disabled-color: lighten(@magic-border-color, 14%);
@magic-disabled-checked-color: lighten(@magic-main-color, 30%);

// Common
@keyframes hover-color {
    from {
        border-color: @magic-border-color;
    }

    to {
        border-color: @magic-main-color;
    }
}

.magic-radio,
.magic-checkbox {
    position: absolute;
    display: none;
}

.magic-radio[disabled],
.magic-checkbox[disabled] {
    cursor: not-allowed;
}

.magic-radio + label,
.magic-checkbox + label {
    position: relative;
    display: block;
    padding-left: 30px;
    cursor: pointer;
    vertical-align: middle;

    &:hover {
        &:before {
            animation-duration: 0.4s;
            animation-fill-mode: both;
            animation-name: hover-color;
        }
    }
    &:before {
        position: absolute;
        top: 0;
        left: 0;
        display: inline-block;
        width: @magic-checkbox-size;
        height: @magic-checkbox-size;
        content: '';
        border: 1px solid @magic-border-color;
    }
    &:after {
        position: absolute;
        display: none;
        content: '';
    }
}

.magic-radio[disabled] + label,
.magic-checkbox[disabled] + label {
    cursor: not-allowed;
    color: @magic-disabled-color;
    &:hover,
    &:before,
    &:after {
        cursor: not-allowed;
    }
    &:hover {
        &:before {
            border: 1px solid @magic-disabled-color;
            animation-name: none;
        }
    }
    &:before {
        border-color: @magic-disabled-color;
    }
}

.magic-radio:checked + label,
.magic-checkbox:checked + label {
    &:before {
        animation-name: none;
    }
    &:after {
        display: block;
    }
}

// Checkbox
.magic-checkbox + label {
    &:before {
        border-radius: 3px;
    }
    &:after {
        top: 2px;
        left: 7px;
        box-sizing: border-box;
        width: 6px;
        height: 12px;
        transform: rotate(45deg);
        border-width: 2px;
        border-style: solid;
        border-color: #fff;

        border-top: 0;
        border-left: 0;
    }
}

.magic-checkbox:checked + label {
    &:before {
        border: @magic-main-color;
        background: @magic-main-color;
    }
}

.magic-checkbox:checked[disabled] + label {
    &:before {
        border: @magic-disabled-checked-color;
        background: @magic-disabled-checked-color;
    }
}

// Radio
.magic-radio + label {
    &:before {
        border-radius: 50%;
    }
    &:after {
        top: 6px;
        left: 6px;
        width: @magic-dot-size;
        height: @magic-dot-size;
        border-radius: 50%;
        background: @magic-main-color;
    }
}

.magic-radio:checked + label {
    &:before {
        border: 1px solid @magic-main-color;
    }
}

.magic-radio:checked[disabled] + label {
    &:before {
        border: 1px solid @magic-disabled-checked-color;
    }
    &:after {
        background: @magic-disabled-checked-color;
    }
}

// 多级下拉选择
.dropdown-submenu {
    position: relative;
}

.dropdown-submenu > .dropdown-menu {
    top: 0;
    left: 100%;
    margin-top: -6px;
    margin-left: -1px;
    border-radius: 0 6px 6px 6px;
}

.dropdown-submenu:hover > .dropdown-menu {
    display: block;
}

.dropdown-submenu > a:after {
    display: block;
    content: " ";
    float: right;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
    border-width: 5px 0 5px 5px;
    border-left-color: #ccc;
    margin-top: 5px;
    margin-right: -10px;
}

.dropdown-submenu:hover > a:after {
    border-left-color: #fff;
}

.dropdown-submenu.pull-left {
    float: none;
}

.dropdown-submenu.pull-left > .dropdown-menu {
    left: -100%;
    margin-left: 10px;
    border-radius: 6px 0 6px 6px;
}

.uploader {
    border: 2px dotted #a5a5c7;
    width: 100%;
    color: #92aab0;
    text-align: center;
    vertical-align: middle;
    padding: 30px 0;
    margin-bottom: 10px;
    font-size: 200%;
    cursor: default;
    user-select: none;
}

.uploader div.or {
    font-size: 50%;
    font-weight: bold;
    color: #c0c0c0;
    padding: 10px;
}

.uploader div.browser label {
    background-color: #5a7bc2;
    color: white;
    padding: 6px 0;
    font-size: 40%;
    font-weight: bold;
    cursor: pointer;
    border-radius: 2px;
    position: relative;
    overflow: hidden;
    display: block;
    width: 300px;
    margin: 20px auto 0 auto;
    box-shadow: 2px 2px 2px #888;
}

.uploader div.browser span {
    cursor: pointer;
}

.uploader div.browser input {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    border: solid transparent;
    border-width: 0 0 100px 200px;
    opacity: .0;
    filter: alpha(opacity=0);
    -o-transform: translate(250px, -50px) scale(1);
    -moz-transform: translate(-300px, 0) scale(4);
    direction: ltr;
    cursor: pointer;
}

.uploader div.browser label:hover {
    background-color: #427fed;
}

.uploader .frm-preview {
    padding-left: 15px;
    padding-right: 15px;
    margin-top: 30px;
    .thumbnail {
        margin-bottom: 0;
    }
    .close {
        margin-right: 5px;
    }
}

.fm-label {
    display: inline-block;
    width: 15%;
    padding-right: 15px;
    vertical-align: top;
    padding-top: 4px;
}

.fm-element {
    display: inline-block;
    width: 60%;
}

.fm-element {
    padding: 6px 8px;
}

.fm-item.fm-element {
    padding: 0;
    width: 100%;
}

.fm-radio > .fm-label {
    padding-top: 0;
    margin-top: -2px;
}

.fm-option {
    display: inline-block;
    margin-right: 10px;
}

.fm-option .fm-option-label {
    vertical-align: top;
}

.media-wrapper {
    padding-left: 15%;
    border: 0;
    color: #fff;
}

.fm-datepicker-popup {
    width: 80% !important;
}

.fm-item .fm-photo-wrapper {
    width: 100%;
}

.fm-group-label-wrapper .fm-label {
    width: 70%;
    text-align: left;
}

.fm-wrapper .wp-tabs-panel > .fm-wrapper .fm-group-inner {
    background: #fdfdfd;
}

.fm-tab-bar .wp-tab-active {
    background: #fdfdfd;
}

.fm-tab-bar .fm-tab {
    padding: 8px 15px 8px;
}

ul.add-menu-item-tabs li, ul.category-tabs li, ul.wp-tab-bar li {
    padding: 5px 8px 8px;
}

.fm-group-inner {
    padding: 15px !important;
}

.fm-tab-bar {
    margin-top: 16px !important;
    margin-bottom: 6px !important;
}

.fm-show-group .fm-item,
.fm-group-inner .fm-item {
    border-bottom: 1px solid #ededed;
    margin-bottom: 15px;
    padding-bottom: 15px;
}

.fm-show-group .fm-wrapper:last-child .fm-item,
.fm-group-inner .fm-wrapper:last-child .fm-item {
    border-bottom: 0;
    padding-bottom: 0;
    margin-bottom: 0;
}

/************************************
 * 设置选项样式
 ************************************/
.appearance_page_wizhi_cms_settings {
    .fm-tabbed-vertical {
        margin: 0;
        background: #24292e;
        .wp-tabs-panel {
            margin-left: 0;
            padding: 0;
        }
        .fm-tab-bar {
            margin-top: 0 !important;
            margin-bottom: 0 !important;
            box-shadow: none;
            li {
                a {
                    background-image: none;
                    background-color: #24292e;
                    color: #fff;
                    border-right: 0;
                    text-shadow: none;
                    border-bottom: 0;
                    padding: 10px 15px;
                    &:hover {
                        background-color: #eee;
                        color: #24292e;
                    }
                }
                &.wp-tab-active {
                    a {
                        background-color: #fff;
                        color: #24292e;
                    }
                }
            }
        }
    }
    .fm-label-inline.fm-label-after {
        padding-top: 0;
        margin-top: -2px;
    }
    .fm-checkbox-group {
        display: inline-block;
        padding-top: 4px;
    }
    .fm-wrapper .wp-tabs-panel > .fm-wrapper > .fm-item {
        margin-bottom: 0;
    }
    .fm-tabbed-vertical > .wp-tabs-panel > div > .fm-group > .fm-group-inner {
        min-height: 700px;
    }

    .media-wrapper {
        margin: 5px 0 15px 0;
        padding-left: 0;
        border-left: 0;
        a.fm-delete {
            position: absolute;
        }
    }
    .fm-label-after {
        width: 90%;
    }
}